body {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    background: #f8f8f8;
}

@media only screen and (min-width: 768px) {
    .card {
        max-width: 75%;
        margin: 0 auto; /* Added */
        float: none; /* Added */
    }
}

.justify-text {
    text-align: justify;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
    color: darkcyan;
    text-decoration: none;
}

a:hover {
    color: #005e5e;
}

.section-title {
    text-align: left;
    margin: 20px 0;
}

.key-element {
    text-align: left !important;
    margin: 8px 0;
    font-size: 18px;
    font-weight: 500;
}

.text-left {
    text-align: left !important;
}

.pl-1 {
    padding-left: 15px !important;
}

.word-break {
    word-break: break-word;
}

td {
    vertical-align: top;
    padding: 3px 0;
    min-width: 100px;
}

ul {
    padding-left: 0 !important;
}

@media only screen and (min-width: 768px) {
    ul {
        padding-left: 2rem !important;
    }

    .p-l-2 {
        padding-left: 2rem !important;
    }
}

li {
    list-style: none;
}

b {
    font-weight: 500;
}

.text-small {
    font-size: 15px;
}

.info-icon {
    color: gray;
    font-size: 13px;
}

.quote-style {
    border-left: .20rem solid darkcyan;
    border-radius: 5px;
    margin-bottom: 8px;
}

.opinion-quote {
    padding-left: 0 !important;
}

@media only screen and (min-width: 768px) {
    .opinion-quote {
        padding-left: 2rem !important;
    }

    .p-l-2 {
        padding-left: 2rem !important;
    }
}

.width-20 {
    width: 10%;
}

@media only screen and (min-width: 768px) {
    .force-center {
        text-align: center !important;
    }
}